import Vue from 'vue'
import VueRouter from 'vue-router'
import goTo from 'vuetify/es5/services/goto'
import HomePage from '../components/HomePage.vue'
import { easeInOutCubic } from 'vuetify/es5/services/goto/easing-patterns'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomePage
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    //component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    component: () => import(/* webpackChunkName: "about" */ '../components/AboutPage.vue').then(m => m.default)
  },
  {
    path: '/tours',
    name: 'tours',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "tours" */ '../components/ToursPage.vue').then(m => m.default)
  },
  {
    path: '/affiliations',
    name: 'affiliations',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "affiliations" */ '../components/AffiliationsPage.vue').then(m => m.default)
  },
  {
    path: '/testimonials',
    name: 'testimonials',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "testimonials" */ '../components/TestimonialsPage.vue').then(m => m.default)
  },
  {
    path: '/contact',
    name: 'contact',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "contact" */ '../components/ContactPage.vue').then(m => m.default)
  },
  {
    path: '*',
    name: 'catchAll',
    component: HomePage
  }
]

const router = new VueRouter({
  routes,
  base: process.env.BASE_URL,
  history: true,
  mode: 'history',
  scrollBehavior(to, from, savedPosition) {
    // return desired position
    /*
    if (to.hash) {
      return {
        selector: to.hash
      };
    } else if (savedPosition) {
      return savedPosition;
    } else {
      return { x: 0, y: 0 };
    }
    */
    let scrollTo = 0

    if (to.hash) {
      //console.log("to.hash: " + to.hash);
      scrollTo = to.hash;
    } else if (savedPosition) {
      scrollTo = savedPosition.y;
    }

    return goTo(scrollTo, { duration: 500, easing: "easeInOutCubic" });
  }
});

export default router;



