<template>
  <div name="navBar" style="z-index: 1">
    <transition name="slide-fade">
      <v-app-bar
        flat
        :fixed="isAppbarFixed"
        :color="getAppbarColor"
        :src="getAppbarImage"
        height="84"
        extension-height="0"
        v-scroll="onScroll"
      >
        <v-spacer class="hidden-sm-and-down"></v-spacer>
        <v-img
          :src="require('@/assets/images/tfv-logo.png')"
          alt="Travelfreak Vacations"
          max-width="40"
          @click="navigateTo('/')"
        ></v-img>
        <v-toolbar-title class="white--text ml-2" @click="navigateTo('/')">Travelfreak Vacations</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn
          v-for="(link, i) in links"
          :key="i"
          text
          class="white--text hidden-sm-and-down"
          tile
          exact
          @click="navigateTo(link)"
        >{{ link.text }}</v-btn>
        <v-spacer class="hidden-sm-and-down"></v-spacer>

        <v-app-bar-nav-icon class="white--text hidden-md-and-up" @click="navDrawer=!navDrawer" />
        <v-navigation-drawer
          v-model="navDrawer"
          absolute
          temporary
          right
          style="top:55px"
          height="280"
        >
          <v-list nav dense>
            <v-list-item v-for="(link, i) in links" :key="i">
              <v-list-item-title @click="navigateTo(link)">{{ link.text }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-navigation-drawer>
      </v-app-bar>
    </transition>
  </div>
</template>

<script>
import navbar from "@/components/mixins/navbar.js";

export default {
  name: "NavBar",
  mixins: [navbar],
  data: () => {
    return {
      isAppbarFixed: false
    };
  },
  methods: {
    /*
    setAppbarColor(color) {
      //console.log("setAppbarColor: " + color);
      this.appbarColor = color;
    },
    getAppbarImage(image) {
      if (this.$vuetify.breakpoint.lgAndUp) {
        return require("@/assets/images/cover/" + image);
      } else {
        return null;
      }
    },
    */
    onScroll() {
      let s = window.pageYOffset || document.documentElement.scrollTop;
      this.isAppbarFixed = s > 500;
    }
  },
  created() {
    this.navigateTo(this.$route.path);
  }
};
</script>

<style scoped>
.v-btn {
  text-transform: none;
}
.v-toolbar {
  font-weight: 700;
  font-size: 20px;
}
</style>


