import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export const store = new Vuex.Store({
    state: {
        appbarColor: "blue darken-4",
        appbarImage: "backgnd_1_navbar.jpg"
    },
    getters: {
        getAppbarColor: state => {
            return state.appbarColor;
        },
        getAppbarImage: state => {
            return state.appbarImage;
        }
    },
    mutations: {
        setAppbarColor: (state, color) => {
            state.appbarColor = color;
        },
        setAppbarImage: (state, image) => {
            state.appbarImage = image;
        }
    }
});


