import router from "@/router";

export default {
    data: () => {
        return {
            links: [
                { path: "/", text: "Home", appbarColor: "blue darken-4", appbarImage: "backgnd_1_navbar.jpg" },
                { path: "/about", text: "About", appbarColor: "cyan accent-4", appbarImage: "backgnd_2_navbar.jpg" },
                {
                    path: "/tours",
                    text: "Tours",
                    appbarColor: "light-blue darken-2", appbarImage: "backgnd_5_navbar.jpg"
                },
                {
                    path: "/affiliations",
                    text: "Affiliations",
                    appbarColor: "orange darken-4", appbarImage: "backgnd_3_navbar.jpg"
                },
                {
                    path: "/testimonials",
                    text: "Testimonials",
                    appbarColor: "blue-grey lighten-1", appbarImage: "backgnd_6_navbar.jpg"
                },
                { path: "/contact", text: "Contact", appbarColor: "#0270A0", appbarImage: "backgnd_4_navbar.jpg" }
            ],
            navDrawer: false
            //appbarImage: "backgnd_1_navbar.jpg"
            //appbarColor: "blue darken-4"
        };
    },
    computed: {
        getAppbarColor() {
            return this.$store.getters.getAppbarColor;
        },
        getAppbarImage(image) {
            if (this.$vuetify.breakpoint.lgAndUp) {
                return require("@/assets/images/cover/" + this.$store.getters.getAppbarImage);
            } else {
                return null;
            }
        },
    },
    methods: {
        setAppbarColor(color) {
            //console.log('setAppbarColor: ' + color);
            this.$store.commit('setAppbarColor', color);
        },
        setAppbarImage(image) {
            //console.log('setAppbarImage: ' + image);
            this.$store.commit('setAppbarImage', image);
        },
        navigateTo(link) {
            // Also, update the Navigation Appbar color/image accordingly..

            this.navDrawer = false;

            let linkObj = link;
            if (typeof link == 'string') {
                let found = false;
                for (let i in this.links) {
                    if (link === this.links[i].path) {
                        linkObj = this.links[i];
                        found = true;
                        //console.log("Link-string converted: " + this.links[i].path);
                        break;
                    }
                }
                if (!found) {
                    //console.log("Link not found: " + link);
                    return;
                }
            }

            //console.log("router.path: " + this.$route.path + ", link.path: " + link.path);
            if (this.$route.path !== linkObj.path) {
                //console.log("Navigating to: " + link.path);
                router.push({ path: linkObj.path });
            }

            this.setAppbarImage(linkObj.appbarImage);
            this.setAppbarColor(linkObj.appbarColor);
        }
    }
}


