      ],
      windowTestimonials: 0,
      autorunTestimonials: true
    };
  },
  computed: {
    featuredDestinations_rowCount: function() {
      return (
        Math.floor(this.featuredDestinations.length / 4) +
        (this.featuredDestinations.length % 4 > 0 ? 1 : 0)
      );
    }
  },
  created() {
    setInterval(() => {
      if (!this.autorunTestimonials) return;
      if (++this.windowTestimonials >= this.testimonials.length)
        this.windowTestimonials = 0;
    }, 4000);
  }
};
