<template>
  <!--
  <v-img
    :src="require('@/assets/images/cover/' + backgroundImage)"
    aspect-ratio="10/4.5"
    min-height="400"
    alt="Travelfreak Vacations"
  >-->
  <v-parallax
    :src="require('@/assets/images/cover/' + backgroundImage)"
    height="550"
    alt="Travelfreak Vacations"
  >
    <v-container fill-height v-scroll="onScroll">
      <v-row>
        <v-col>
          <transition appear name="slide-fade">
            <v-overlay
              :style="'position:relative; opacity:' + scrollOpacity + ';'"
              color="rgba(f, f, f, 1)"
              :class="(page!=null) ? 'align-items-center justify-content-start' : 'justify-start'"
            >
              <p style="word-spacing:20px;" v-if="(page!=null)">
                <v-btn text @click="navigateTo('/');">HOME</v-btn>
                > {{ page.toUpperCase() }}
              </p>
              <h1>
                <strong>{{ title }}</strong>
                <p style="font-weight: normal">{{ title2 }}</p>
              </h1>
              <h3>{{subTitle}}</h3>
              <br />
              <br />
            </v-overlay>
          </transition>
        </v-col>
      </v-row>
    </v-container>
  </v-parallax>
</template>

<script>
import router from "@/router";

export default {
  name: "CoverImage",
  props: ["backgroundImage", "page", "title", "title2", "subTitle"],
  data: () => {
    return {
      scrollOpacity: 1
    };
  },
  methods: {
    onScroll() {
      let s =
        window.top.scrollY ||
        document.documentElement.scrollTop ||
        window.pageYOffset;
      //let s = window.scrollY;
      let op = 1 - s / 600;
      //alert(s, op);
      op = op > 1 ? 1 : op;
      op = op < 0 ? 0 : op;

      this.scrollOpacity = op;
      //console.log("scroll opacity: " + this.scrollOpacity);
    },
    navigateTo(path) {
      router.push(path);
    }
  }
};
</script>

<style scoped>
.v-parallax__image {
  transform: none !important;
  width: 100% !important;
}
</style>


